<template>
  <div class="shopbag">
    <div class="nav-bg"></div>
    <van-nav-bar
      title="购物袋"
      left-text="返回"
      :right-text="isEdit ? '完成' : '编辑'"
      left-arrow
      :fixed="true"
      @click-right="editProduct"
    />
    <div v-if="shopbagData.length > 0">
      <div class="shopbag-content">
        <van-swipe-cell
          :disabled="!isEdit"
          class="shopbag-box"
          v-for="( item , index ) in shopbagData"
          :key="index"
        >
          <van-cell>
            <div class="clearfix cell-box">
              <div class="fl check">
                <van-checkbox v-model="item.isCheck" checked-color="#0C34BA" @click="simpleSelect"></van-checkbox>
              </div>

              <div class="fl pro-img">
                <img class="auto-img" :src="item.small_img" @click="viewProductInfo(item.pid)" alt />
              </div>

              <div class="fl pro-info">
                <div class="text-box">
                  <div class="clearfix" @click="viewProductInfo(item.pid)">
                    <div class="fl text-name">{{item.name}}</div>
                    <div class="fl text-rule">{{item.rule}}</div>
                  </div>
                  <div class="text-enname">{{item.enname}}</div>
                </div>
                <div class="price-box clearfix">
                  <div class="fl price">￥{{item.price}}</div>
                  <div class="fr">
                    <van-stepper
                      v-model="item.count"
                      theme="round"
                      button-size="24"
                      disable-input
                      @change="modifyCount(item)"
                    />
                  </div>
                </div>
              </div>
            </div>
          </van-cell>

          <template #right>
            <van-button square color="#3D75FE" text="删除" @click="removeOneShopbag(item.sid,index)" />
          </template>
        </van-swipe-cell>
      </div>

      <van-submit-bar
        v-show="!isEdit"
        @submit="goPay"
        :price="total"
        button-text="提交订单"
        button-color="#0C34BA"
      >
        <van-checkbox
          v-model="allCheck"
          text-align="left"
          icon-size="24px"
          checked-color="#0C34BA"
          @click="allSelect"
        >全选</van-checkbox>
      </van-submit-bar>

      <van-submit-bar
        v-show="isEdit"
        button-text="删除选择"
        button-color="#0C34BA"
        @submit="removeMoreShopbag"
      >
        <van-checkbox
          v-model="allCheck"
          icon-size="24px"
          checked-color="#0C34BA"
          @click="allSelect"
        >全选</van-checkbox>
      </van-submit-bar>
    </div>
    <div v-else class="bag_box">
      <van-empty description="购物车空空如也!赶快去逛逛吧">
        <van-button round type="danger" class="bottom-button" @click="goMenu">逛一逛</van-button>
      </van-empty>
    </div>
  </div>
</template>

<script>
import "../../assets/less/shopbag.less";
import { utils } from "../../assets/js/utils";
export default {
  data() {
    return {
      isEdit: false,
      checked: false,
      shopbagData: [],
      allCheck: false,
      total: 0,
    };
  },
  created() {
    // 查询购物车数目
    this.findShopbag();
  },
  methods: {
    //回到菜单
    goMenu() {
      this.$router.push({ name: "Menu" });
     
    },
    //提交订单
    goPay() {
      // 查找需要购买的商品.sid
      let sids = [];

      this.shopbagData.map((v) => {
        if (v.isCheck) {
          // 将选中的添加到数组中
          sids.push(v.sid);
        }
      });
      // 将选中的数组转为字符串发送
      sids = sids.join("-");

      if (sids.length != 0) {
        this.$router.push({ name: "Pay", query: { sids } });
      } else {
        this.$toast("请选择购买的商品");
      }
      // 
    },
    // 编辑商品
    editProduct() {
      this.isEdit = !this.isEdit;
    },
    //查看商品详情
    viewProductInfo(pid) {
      this.$router.push({ name: "Detail", query: { pid } });
    },
    //查询购物袋数目
    findShopbag() {
      // 获取token字符串
      let tokenString = localStorage.getItem("CSTK");
      if (!tokenString) {
        return;
      }

      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
        loadingType: "spinner",
      });

      this.axios({
        method: "GET",
        url: "/findAllShopcart",
        params: {
          appkey: this.appkey,
          tokenString,
        },
      })
        .then((result) => {
          
          this.$toast.clear();
          if (result.data.code == 5000) {
            result.data.result.map((v) => {
              // 
              v.isCheck = false;
            });
            this.shopbagData = result.data.result;
          } else {
            this.$toast(result.data.msg);
          }
        })
        .catch((err) => {
          this.$toast.clear();
        });
    },
    //全选
    allSelect() {
      // 
      this.shopbagData.map((v) => {
        v.isCheck = this.allCheck;
      });
      // 计算总价
      this.sum();
    },
    // 单选
    simpleSelect() {
      this.sum();
      for (let i = 0; i < this.shopbagData.length; i++) {
        if (!this.shopbagData[i].isCheck) {
          this.allCheck = false;
          return;
        }
      }
      this.allCheck = true;
      // 计算总价
    },
    // 修改商品数量
    modifyCount(item) {
      //获取token字符串
      let tokenString = localStorage.getItem("CSTK");
      if (!tokenString) {
        return;
      }

      //参数序列化
      let data = utils.queryString({
        appkey: this.appkey,
        tokenString,
        sid: item.sid,
        count: item.count,
      });

      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
        loadingType: "spinner",
      });

      this.axios({
        method: "POST",
        url: "/modifyShopcartCount",
        data,
      })
        .then((result) => {
          this.$toast.clear();
          // 
          if (result.data.code !== 6000) {
            this.$toast(result.data.msg);
          }
          // 计算总价
          this.sum();
        })
        .catch((err) => {
          this.$toast.clear();
          // 
        });
    },
    // 删除购物车数据
    removeShopbag(sids) {
      //sids: sid的集合，类型：array

      //获取token字符串
      let tokenString = localStorage.getItem("CSTK");
      if (!tokenString) {
        return;
      }

      // 参数序列化
      let data = utils.queryString({
        appkey: this.appkey,
        tokenString,
        sids: JSON.stringify(sids),
      });

      // 加载动画
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
        loadingType: "spinner",
      });

      return this.axios({
        method: "POST",
        url: "/deleteShopcart",
        data,
      });
    },
    //删除单个商品
    removeOneShopbag(sid, index) {
      this.removeShopbag([sid])
        .then((result) => {
          // 关闭加载动画
          this.$toast.clear();
          // 

          if (result.data.code == 7000) {
            this.shopbagData.splice(index, 1);
          }

          // 提示删除成功
          this.$toast(result.data.msg);

          // 计算总价
          this.sum();
        })
        .catch((err) => {
          this.$toast.clear();
          // 
        });
    },
    //  删除多个商品
    removeMoreShopbag() {
      // 查找勾选的商品
      let sids = [];
      this.shopbagData.map((v) => {
        if (v.isCheck) {
          sids.push(v.sid);
        }
      });

      // 调用删除方法
      this.removeShopbag(sids)
        .then((result) => {
          // 
          this.$toast.clear();
          if (result.data.code == 7000) {
            // 遍历所有商品
            for (let i = this.shopbagData.length - 1; i >= 0; i--) {
              // 判断是否为选中
              if (this.shopbagData[i].isCheck) {
                // 将选中的商品删除
                this.shopbagData.splice(i, 1);
              }
            }
          }

          // 提示框提示
          this.$toast(result.data.msg);

          // 计算总价
          this.sum();
        })
        .catch((err) => {
          // 
          this.$toast.clear();
        });
    },
    // 计算商品总额
    sum() {
      // 设置总价钱
      this.total = 0;
      // 遍历商品
      this.shopbagData.map((v) => {
        // 判断是否选中
        if (v.isCheck) {
          this.total += v.count * v.price;
        }
      });
      this.total *= 100;
    },
  },
};
</script>